import { IControl } from "./icontrol";
import { ControlType } from "./../controltype";

/**
 * 
 */
export class FlexibleNumberControl implements IControl{

    /**
     * 
     */
    getControlType(): ControlType {
        return ControlType.FlexibleNumber;
    }

    /**
     * 
     */
    length:number;

    /**
     * 
     */
    precision:number;
    /**
     * 
     */
    placeholder:string;
    beginPlaceHolder:string;
    endPlaceHolder:string;
    /**
     * 
     */
    isExtend:boolean;

    /**label */
    showLabel:boolean;

    /** */
    required:boolean;
    /** */
    bigNumber:boolean;
    /** */
    single:boolean;
}