import { Component, Input, OnInit, OnChanges, EventEmitter, Output, HostBinding} from '@angular/core';
import { QueryCondition } from '../../entity/querycondition';
import { SolutionService } from '../../service/solution.service';
import { InputGroupControl } from '../../entity/controltype/input-group/inputgroupcontrol';
import { InputGroupValue } from '../../entity/conditionvalue/inputgroup';


@Component({
    selector: 'app-filter-inputgroup',
    templateUrl: './inputgroup.component.html',
})

export class SimpleInputGroupComponent implements OnInit{
    /**
     * QueryCondition
     */
    _item: QueryCondition;

    testId: string;

    usageMode: string;

    dialogOptions: any;

    @Input()
    set item(textItem: any) {
        this._item = textItem;
    }
    get item(): any{
        return this.constructTextItem(this._item);
    }
    @Input() autoLabel:boolean;
    @Input() disabled:boolean;

    /** */
    extend:boolean = false;
    /**label */
    showlabel:boolean = true;
    
    @HostBinding('class.filter-item-wrap')
    get isExtend(): boolean {
        return !this.extend && this.autoLabel;
    }
    @HostBinding('class.filter-item-hiddenlabel')
    get hidenLabel(): boolean {
        return !this.showlabel;
    }

    @Output('searchHandle') searchHandle = new EventEmitter();

    textField: string;
    isTextFromDialog: boolean = false;
    textFormDialog: string;

    constructTextItem(textItem: QueryCondition): QueryCondition {
        const textValueObj = textItem.value as InputGroupValue;
        textValueObj.textValue = textValueObj.textValue && textValueObj.textValue.trim();
        textItem.value = textValueObj;
        return textItem;
    }

    constructor(private solutionService:SolutionService){}

    ngOnInit(): void {
        this.testId = this.solutionService.getGuid();
        this.getInputGroupBindingInfo();

        if(this.item && this.item.control && this.item.control.hasOwnProperty('isExtend')){
            this.extend = this.item.control.isExtend;
            if(!this.extend){
                this.showlabel = this.item.control.showLabel;
            }
        }
    }

    getInputGroupBindingInfo() {
        const inputGroupControl = (this.item.control as InputGroupControl);
        this.usageMode = inputGroupControl.usageMode;
        if(this.usageMode == 'open-modal' || this.usageMode == 'open-remote-modal') {
            this.dialogOptions = inputGroupControl.dialogOptions;
            /*
                example of dialogOptions = {
                   modalId: "1e011720-eb0f-4c9c-82b6-6844c89b2303"
                   mapFields: {hDepartment: "Name"}
                   title: ""
                   width: 800
                   height: 600
                }
             */
            if(this.dialogOptions.mapFields){
                this.textField = JSON.stringify(this.dialogOptions.mapFields).split('"')[1];
            }
        }
    }

    getConditionItemTitle(field:QueryCondition){
        return this.solutionService.getConditionItemTitle(field);
    }
    
    /*
        dialogOptions = {
            modalId: "ae011720-eb0f-4c9c-82b6-6844c89b2303"
            mapFields: {hDepartment: "Name"}
            title: ""
            width: 800
            height: 600
            showButtons: true
            buttons: [
                { text: "", cls: "btn-primary", handle: _this.viewModel.rootviewmodelconfirm1 },
                { text: "", cls: "btn-secondary", handle: _this.viewModel.rootviewmodelcancel1}
            ]
        }
    */
   onClick(event: any){
        if(this.usageMode == 'text'){
            this.item.control.click();
        }
        else{
            const options = this.dialogOptions;
            options.handle = (selectedItem) => {
                // let modalData = item[0];
                // this.item.value.value = modalData[this.targetField];
                let selectedItems = selectedItem.filter(n => n);
                if(selectedItems && this.textField){
                    this.item.value.value = selectedItems;
                    this.item.value.textValue = this.item.value.getTextValue(this.textField);
                    this.item.value.textField = this.textField;
                    this.item.value.isInputText = false;

                    this.isTextFromDialog = true;
                    this.textFormDialog = this.item.value.textValue;
                }
            }
            this.item.control.click({event, options});
        }
    }

    onBlur(event){
        if(this.disabled){return;}
        if(this.usageMode == 'text') {
            this.item.value.isInputText = true;
        }
        if(this.usageMode == 'open-modal' || this.usageMode == 'open-remote-modal') {
            if(!this.isTextFromDialog) {
                this.item.value.isInputText = true;
            }
            if(this.item.value.textValue != this.textFormDialog){
                this.isTextFromDialog = false;
                this.item.value.value = [];
                this.item.value.textField = undefined;
                this.item.value.isInputText = true;
            }
        }
        this.searchHandle.emit(event);
    }
}