import { IValue } from './ivalue';
import { ControlType } from '../controltype';
import { EventEmitter } from '@angular/core';

export class FlexibleNumberValue implements IValue {
    startValue: number;
    endValue: number;
    value: number;
    single: boolean;
    clear$ = new EventEmitter();

    constructor(value: { startValue: number, endValue: number, value: number } = { startValue: null, endValue: null, value: null }, single: boolean = true) {
        this.startValue = value.startValue;
        this.endValue = value.endValue;
        this.value = value.value;
        this.single = single;
    }

    getControlType(): ControlType {
        return ControlType.FlexibleNumber;
    }

    isEmpty(): boolean {
        if (this.single) {
            return !this.value;
        }
        else return (this.startValue == null && this.endValue == null);
    }
    clearValue(): void {
        this.startValue = undefined;
        this.endValue = undefined;
        this.value = undefined;
        this.clear$.emit();
    }
    setOriginalValue(value: any): void {
        throw new Error("Method not implemented.");
    }
    getOriginalValue() {
        return {
            begin: this.startValue,
            end: this.endValue,
            value: this.value
        };
    }


    getValueText(): any {
        if (this.single) {
            if (this.value == undefined || (!this.value && typeof (this.value) != 'undefined' && this.value != 0)) {
                return this.value;
            }
            else {
                return '' + this.value;
            }
        }
        else {
            if (this.startValue == undefined && this.endValue == undefined) {
                return '';
            }
            else if (this.startValue == undefined) {
                return ('~' + this.endValue);
            }
            else if (this.endValue == undefined) {
                return (this.startValue + '~');
            }
            else {
                return (this.startValue + '~' + this.endValue);
            }
        }
    }
}
