export function deepCopy(obj) {
    var newobj = obj.constructor === Array ? [] : {};
    if (typeof obj !== 'object') {
      return obj;
    } else {
        for (var i in obj) {
            if (typeof obj[i] === 'object'){ //
                const type = Object.prototype.toString.call(obj[i]);
                if(type == '[object Null]'){
                    newobj[i] = obj[i];
                }
                else{
                    newobj[i] = deepCopy(obj[i]);  //
                }
            }else{
                newobj[i] = obj[i];
            }
        }
    }
    return newobj; //
}  