import { IControl } from "../icontrol";
import { ControlType } from "../../controltype";
import { DisplayType } from './displayType';

/**
 * 
 */
export class HelpContol implements IControl{

    /**
     * 
     */
    getControlType(): ControlType {
        return ControlType.Help;
    }

    /**
     * 
     */
    placeholder:string;
    /**
     * 
     */
    uri:string;

    /**
     * 
     */
    textField:string;

    /**
     * 
     */
    valueField:string;

    idField:string;
    helpId:string;
    displayType:DisplayType;
    /**
     * 
     */
    mapFields:any;
    /**
     * 
     */
    isExtend:boolean;

    /**label */
    showLabel:boolean;

    /** */
    required:boolean;

    /**
     * 
    */
   preEventCmd: any;

   /**
   * 
   */
   postEventCmd: any;

   /**
     * 
    */
   context: any;
    /**  */
   enableFullTree: boolean;
   /**  default:  loadall:  layerload */
   loadTreeDataType: 'default' | 'loadall' | 'layerload';
    /** */
   singleSelect: boolean;
   /** */
   expandLevel: number;
   /**  */
   enableCascade: boolean;
}