import { IControl } from "./icontrol";
import { ControlType } from "../controltype";

/**
 * 
 */
export class FlexibleDateControl implements IControl{

    /**
     * 
     */
    getControlType(): ControlType {
        return ControlType.FlexibleDate;
    }

    /**
     * 
     */
    length:number;

    /**
     * 
     */
    placeholder:string;
    beginPlaceHolder:string;
    endPlaceHolder:string;
    /**
     * 
     */
    isExtend:boolean;

    /**label */
    showLabel:boolean;

    /** */
    required:boolean;
    /** */
    single:boolean;
    showType:string;
    /**
     * 
     */
    format:string;
    showTime:boolean;
}